@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-outline{
    border: 1px solid $black;
    font-size: 18px;
    color: $black;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}
